
















import Panel from 'vux/src/components/panel/index.vue'

var refresh = ['A','B01','B02','B03','B04',
'B05','B06','B07','B08','B09','B010'];

var key = 0;
var keyvalue = 'A';
var initLoaded = false; // 初始化数据是否加载完成
var start = 0;
var end = start + 9;

function getrefresh(){
key ++;
if(key == refresh.length){
key = 0;
}
keyvalue = refresh[key];
}

export default {
components: {
Panel
},
data(){
return {
type: '1',
panellist: [],
morelist:[]
}
},
created(){

this.$jsonp('https://3g.163.com/touch/jsonp/sy/recommend/0-9.html',{})
.then((data) => {

this.panellist = data.list.filter( item => {
return item.addata === null && item.picInfo[0];
} ).map(item => {
return {
src: item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: {
path: '/',
replace: false
}
}
})
initLoaded = true;
})
.catch((error) => {
console.log(error)
})
},
methods: {
refresh(){  //上拉刷新

getrefresh();
this.$jsonp('https://3g.163.com/touch/jsonp/sy/recommend/0-9.html',{
miss: '00',
refresh: keyvalue
})
.then((data) => {

this.panellist = data.list.filter( item => {
return item.addata === null && item.picInfo[0];
} ).map(item => {
return {
src: item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: {
path: '/',
replace: false
}
}
})

this.$refs.myref.finishPullToRefresh();

this.$vux.toast.text(`刷新了${this.panellist.length}条数据`, 'top')
})
.catch((error) => {
console.log(error)
})
},
infinite(){
if(!initLoaded){
this.$refs.myref.finishInfinite();
return;
}

this.$jsonp(`https://3g.163.com/touch/jsonp/sy/recommend/${start}-${end}.html`,{
miss: '00',
refresh: keyvalue
})
.then((data) => {

var newmorelist = data.list.filter(item => {
return item.addata === null && item.picInfo[0];
}).map(item => {
return {
src: item.picInfo[0].url,
title: item.title,
desc: item.digest,
url: {
path: '/',
replace: false
}
}
})
this.morelist = this.morelist.concat(newmorelist);
start += 10;
end = start + 9;
this.$refs.myref.finishInfinite();

})


}
}
}



